@import "../../css-config/mixins.scss";

.cart-panel {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 99;

    .opac-layer {
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .cart-content {
        height: 100%;
        background: white;
        position: relative;
        display: flex;
        z-index: 1;
        flex-direction: column;
        transform: translateX(100%);
        animation: slideCartWindow 0.3s ease forwards;
        width: 100%;


        @include md {
            width: 340px;
        }

        .cart-header {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 20px 15px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.5);

            .heading {
                flex-grow: 1;
                margin-bottom: 0;
                font-size: 20px;
                font-weight: 700;
                text-transform: uppercase;
            }

            .close-btn {
                display: flex;
                align-items: center;
                gap: 5px;
                cursor: pointer;
                font-size: 14px;

                &:hover {
                    opacity: 50;
                }
            }

            .svg1 {
                font-size: 18px;
            }

            .text {
                margin-bottom: 0;
                font-size: 15px;
                font-weight: 700;
                text-transform: uppercase;
            }

        }

        .empty-cart {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            margin-top: 100px;

            .svg2 {
                font-size: 120px;
                opacity: 0.6;
            }

            .return-cta {
                outline: 0;
                border: 0;
                height: 40px;
                width: 150px;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                font-size: 13px;
                color: white;
                background: #8e2de2;
                border-bottom: 3px solid #6515aa;
            }


        }


        .cart-footer {
            border-top: 1px solid rgba(0, 0, 0, 0.1);

            .subtotal {
                padding: 20px 15px;
                border-bottom: 1px solid rgba(0, 0, 0, 0.1);
                display: flex;
                justify-content: space-between;

                .textCart {
                    //
                }

                .total {
                    color: #8e2de2;
                }
            }

            .button1 {
                padding: 20px 15px;

                .checkout-cta {
                    outline: 0;
                    border: 0;
                    height: 50px;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-size: 13px;
                    color: white;
                    background: #8e2de2;
                    border-bottom: 3px solid #6515aa;
                }
            }
        }
    }

}

@keyframes slideCartWindow {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(0%);
    }

}